/*   */
.step2Container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #f9f9f9;
  height: auto;
}

/*  */
.header {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
  text-align: center;
}

/*   */
.uploadContainer {
  display: flex;
  gap: 20px; /*    */
  flex-wrap: wrap; /*   */
  justify-content: center;
}

/*  */
.card {
  width: 300px;
  height: 250px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease; /*    */
  position: relative;
}

/*   */
.cardSelected {
  background-color: #e0f7fa; /*    */
  transform: scale(1.05); /*   */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /*    */
}

/*  */
.iconContainer {
  width: 60px;
  height: 60px;
  margin-bottom: 15px;
}

.icon {
  width: 100%;
  height: 100%;
}

/*    */
.cardHeader {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 15px;
  text-align: center;
}

/*   */
.successMessage {
  color: #2e7d32; /*   */
  font-size: 1rem;
  margin-top: 10px;
  animation: fadeIn 0.5s ease;
}

/*  fade-in */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/*    */
.fileInput {
  display: none;
}

/*   */
.uploadButton {
  padding: 10px 20px;
  background-color: #007bff; /*   */
  color: white;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

/*    */
.uploadButton:hover {
  background-color: #0056b3;
}
