/*   */
.container {
  padding: 20px;
  background-color: #f9f9f9;
}

/*  */
.heading {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

/*    */
.createButton {
  display: block;
  margin: 0 auto 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.createButton:hover {
  background-color: #0056b3;
}

/*    */
.albumsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  justify-items: center; /*     */
}

/*   */
.albumCard {
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  width: 90%; /*   */
  text-align: center; /*    */
}

.albumCard:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

/*  */
.imageContainer {
  position: relative;
  width: 80%; /*   */
  margin: 0 auto;
  top: -20px; /*      */
  z-index: 1;
}

.coverImage {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/*     */
.imageSeparator {
  width: 100%;
  height: 7px;
  background-color: #007bff;
  margin-top: -7px; /*       */
  z-index: 0;
}

/*   */
.albumTitle {
  font-size: 1.2rem;
  margin: 10px 0;
  color: #333;
}

.albumDetails {
  font-size: 0.9rem;
  color: #555;
  margin: 5px 0;
}

/*   */
.viewButton,
.deleteButton {
  display: inline-block;
  width: 45%;
  margin: 10px 2.5%;
  padding: 8px 0;
  font-size: 1rem;
  text-align: center;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.viewButton {
  background-color: #28a745;
  color: white;
}

.viewButton:hover {
  background-color: #218838;
}

.deleteButton {
  background-color: #dc3545;
  color: white;
}

.deleteButton:hover {
  background-color: #c82333;
}
.selfieButton {
  margin-top: 10px;
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.selfieButton:hover {
  background-color: #0056b3;
}
